@import "./media.scss";

.introduction {
	width: 100%;
	background-image: url("../img/BG.jpg");
	background-position: center;
	background-size: cover;

	@include for-phone-only {
		height: 844px;
	}
	@include for-tablet-portrait-up {
		height: 1180px;
	}
	@include for-tablet-landscape-up {
		height: 820px;
	}
	@include for-desktop-up {
		height: 900px;
	}
	@include for-big-desktop-up {
		height: 900px;
	}
}

.introduction__wrapper {
	@include for-phone-only {
		padding-top: 200px;
	}
	@include for-tablet-portrait-up {
		padding-top: 350px;
	}
	@include for-tablet-landscape-up {
		padding-top: 250px;
				
	}
	@include for-desktop-up {
		padding-top: 320px;
		
	}
	@include for-big-desktop-up {
		padding-top: 320px;

	}
}

.introduction__text {
	display: flex;
	flex-direction: column;
	@include for-phone-only {
		row-gap: 80px;
	}
	@include for-tablet-portrait-up {
		row-gap: 50px;
		
	}
	@include for-tablet-landscape-up {
		row-gap: 80px;
				
	}
	@include for-desktop-up {
		row-gap: 50px;
	}
	@include for-big-desktop-up {
		row-gap: 50px;

	}
}

.introduction__text-bigger {
	@include for-phone-only {
		font-size: 1.6rem;
	
	}
	@include for-tablet-portrait-up {
		font-size: 2.5rem;
		line-height: 1.4;
	}
	@include for-tablet-landscape-up {
		font-size: 2.5rem;
				
	}
	@include for-desktop-up {
		font-size: 2.6rem;
		width: 70%;
	}
	@include for-big-desktop-up {
		font-size: 2.6rem;

	}
}

.introduction__text-smaller {
	@include for-phone-only {
		font-size: 1.2rem;
	}
	@include for-tablet-portrait-up {
		font-size: 1.5rem;
		
	}
	@include for-tablet-landscape-up {
		font-size: 1.4rem;
				
	}
	@include for-desktop-up {
		font-size: 1.4rem;
		
	}
	@include for-big-desktop-up {
		font-size: 1.4rem;

	}
}

.introduction__btn {
	border: 1px solid #fff;
	text-align: center;
	transition: all 0.5s;
	cursor: pointer;
	text-decoration: none;
	color: #dedede;
	display: block;
	&:hover {
		// background-color: #dedede;
		// color: #000;
		backdrop-filter: blur(9px);
	}

	@include for-phone-only {
		font-size: 1rem;
		width: 75%;
		padding: 20px 0;
		margin-top: 60px;
	}
	@include for-tablet-portrait-up {
		font-size: 1.2rem;
		width: 60%;
		padding: 25px 0;
		margin-top: 100px;
	}
	@include for-tablet-landscape-up {
		font-size: 1.2rem;
		width: 480px;
		padding: 30px 0;
		margin-top: 50px;	
	}
	@include for-desktop-up {
		font-size: 1.2rem;
		width: 480px;
		padding: 30px 0;
		margin-top: 70px;
	}
	@include for-big-desktop-up {
		font-size: 1.2rem;
		width: 480px;
		padding: 30px 0;
		margin-top: 70px;
	}
}