@import "./media.scss";

@include for-phone-only {
	.about {
		height: 844px;

		&__title {
			font-size: 2.2rem;
			text-align: center;
			padding-top: 74px;
		}

		&__text {
			position: relative;
			padding-left: 20px;
			font-size: 1rem;
			margin-top: 75px;
			&::before {
				content: "";
				height: 100%;
				width: 5px;
				position: absolute;
				left: 0;
				background-color: #AA2330;
			}
		}

		&__slogan {
			margin-top: 130px;
			font-size: 1.5rem;
		}

		&__slogan-first {
			font-family: "Helvetica";
		}

		&__slogan-second {
			text-transform: uppercase;
			font-size: 1.1rem;
		}
	}
}
@include for-tablet-portrait-up {
	.about {
		height: 900px;

		&__title {
			font-size: 2.2rem;
			text-align: center;
			padding-top: 80px;
		}

		&__text {
			position: relative;
			padding-left: 30px;
			width: 80%;
			font-size: 1rem;
			margin-top: 75px;
			&::before {
				content: "";
				height: 100%;
				width: 5px;
				position: absolute;
				left: 0;
				background-color: #AA2330;
			}
		}

		&__slogan {
			margin-top: 140px;
			font-size: 2.5rem;
		}

		&__slogan-first {
			font-family: "Helvetica";
		}

		&__slogan-second {
			text-transform: uppercase;
			font-size: 1.6rem;
		}
	}
}
@include for-tablet-landscape-up {
	.about {
		height: 820px;

		&__title {
			font-size: 2.5rem;
		}

		&__text {
			width: 70%;
			line-height: 1.8;
		}

		&__slogan {
			font-size: 2.5rem;
		}

		&__slogan-first {
		}

		&__slogan-second {
			font-size: 1.8rem;
		}
	}	
}
@include for-desktop-up {
	.about {
		height: 750px;

		&__title {
			font-size: 3rem;
		}

		&__text {
			width: 70%;
			line-height: 1.8;
			// margin-left: 100px;
		}

		&__slogan {
			font-size: 3rem;
			// margin-left: 100px;
		}

		&__slogan-first {
		}

		&__slogan-second {
			font-size: 2.2rem;
		}
	}	
}
