@media (prefers-reduced-motion: no-preference) {
	:root {
		scroll-behavior: smooth; 
	}
}

* {
	@include for-phone-only {
		line-height: 1.6;
	}
	@include for-tablet-portrait-up {
		line-height: 1.6;
	}
	@include for-tablet-landscape-up {

	}
	@include for-desktop-up {
		
	}
	@include for-big-desktop-up {

	}
}


html {
	@include for-phone-only {
		font-size: 16px;
	}
	@include for-tablet-portrait-up {
		font-size: 18px;
	}
	@include for-tablet-landscape-up {
		font-size: 18px;		
	}
	@include for-desktop-up {
		font-size: 18px;
	}
	@include for-big-desktop-up {
		font-size: 18px;
	}
}

body {
	all: unset;
	font-family: "Tenor";
	color: #EDEDED;
	overflow-x: hidden;
	width: 100%;
	height: 100%;
	background-color: #111214;
}

.main {
	width: 100%;
}

.container {
	height: 100%;
	max-width: 1600px;
	margin: 0 auto;
	@include for-phone-only {
		padding: 0 20px;
	}
	@include for-tablet-portrait-up {
		padding: 0 50px;
	}
	@include for-tablet-landscape-up {
		padding: 0 50px;
	}
	@include for-desktop-up {
		padding: 0 50px;
	}
	@include for-big-desktop-up {
		padding: 0 10%;
	}
}