@import "./media.scss";

.slider {
	@include for-phone-only {
		height: 844px;
	}
	@include for-tablet-portrait-up {
		height: 900px;
	}
	@include for-tablet-landscape-up {
				
	}
	@include for-desktop-up {
		height: 1000px;
		
	}
	@include for-big-desktop-up {

	}

		&__title {
			position: relative;

			&::before {
				content: "";
				position: absolute;
				background-color: #241313;
				z-index: -1;

			}

			@include for-phone-only {
				font-size: 1.3rem;
				margin-left: 48px;
				margin-top: 178px;
				&::before {
					width: 95px;
					height: 95px;
					left: -48px;
					top: -33px;
				}
			}
			@include for-tablet-portrait-up {
				font-size: 1.8rem;
				margin-left: 48px;
				margin-top: 178px;
				&::before {
					width: 130px;
					height: 130px;
					left: -65px;
					top: -37px;
				}
			}
			@include for-tablet-landscape-up {
						
			}
			@include for-desktop-up {
				font-size: 2.2rem;
				margin-left: 100px;
				margin-top: 178px;
				&::before {
					width: 190px;
					height: 190px;
					left: -90px;
					top: -60px;
				}
			}
			@include for-big-desktop-up {

			}
		}

		&__body {
			margin-top: 100px;
			overflow: hidden;
			@include for-phone-only {
	
			}
			@include for-tablet-portrait-up {
				
			}
			@include for-tablet-landscape-up {
						
			}
			@include for-desktop-up {
				
			}
			@include for-big-desktop-up {

			}
		}

		&__body-items {
			width: 100%;
			height: 100%;
			display: flex;
			column-gap: 20%;
			transition: all 0.5s;
			@include for-phone-only {
				
			}
			@include for-tablet-portrait-up {

			}
			@include for-tablet-landscape-up {
						
			}
			@include for-desktop-up {
				
			}
			@include for-big-desktop-up {

			}
		}

		&__body-items-item {
			box-sizing: border-box;
			min-width: 100%;
			max-width: 100%;
			height: 100%;
			display: flex;

			border-bottom: #dedede solid 4px;
			border-right: #dedede solid 4px;
			@include for-phone-only {
				flex-direction: column;
			}
			@include for-tablet-portrait-up {
				padding-top: 60px;
				flex-direction: row;
				align-items: end;
				column-gap: 10px;
			}
			@include for-tablet-landscape-up {
						
			}
			@include for-desktop-up {
				column-gap: 30px;
			}
			@include for-big-desktop-up {

			}
		}

		&__body-items-item-describtion {
			margin-bottom: 10px;
			min-height: 80px;
			@include for-phone-only {
				
			}
			@include for-tablet-portrait-up {
				width: 70%;
			}
			@include for-tablet-landscape-up {
						
			}
			@include for-desktop-up {
				font-size: 1.4rem;
			}
			@include for-big-desktop-up {

			}
		}

		&__body-items-item-img {
			width: 100%;
			background-position: center;
			background-repeat: no-repeat;
			background-size: cover;
			&_1 {
				background-image: url("../img/slider1.jpg");
			}
			&_2 {
				background-image: url("../img/slider2.jpg");
			}
			&_3 {
				background-image: url("../img/slider3.jpg");
			}
			&_4 {
				background-image: url("../img/slider4.jpg");
			}
			&_5 {
				background-image: url("../img/slider5.jpg");
			}
			&_6 {
				background-image: url("../img/slider6.jpg");
			}
			&_7 {
				background-image: url("../img/slider7.jpg");
			}
			&_8 {
				background-image: url("../img/slider8.jpg");
			}
			@include for-phone-only {
				padding-top: 250px;

			}
			@include for-tablet-portrait-up {
				padding-top: 250px;
			}
			@include for-tablet-landscape-up {
				padding-top: 400px;
			}
			@include for-desktop-up {
				padding-top: 500px;
			}
			@include for-big-desktop-up {

			}
		}

		&__controller {
			display: flex;
			column-gap: 20px;
			margin-top: 40px;

			@include for-phone-only {
				justify-content: center;
			}
			@include for-tablet-portrait-up {
				justify-content: flex-end;
			}
			@include for-tablet-landscape-up {
						
			}
			@include for-desktop-up {
				justify-content: center;
			}
			@include for-big-desktop-up {

			}
		}

		&__controller-arrow {
			width: 62px;
			height: 62px;
			cursor: pointer;
			@include for-phone-only {
	
			}
			@include for-tablet-portrait-up {
				
			}
			@include for-tablet-landscape-up {
						
			}
			@include for-desktop-up {
				width: 82px;
				height: 82px;
			}
			@include for-big-desktop-up {

			}

			&_left {
				transform: rotate(90deg);
			}

			&_right {
				transform: rotate(-90deg);
			}
		}

		&__controller-counter {
			letter-spacing: 0.5rem;
			display: flex;
			align-items: center;
			column-gap: 5px;
			@include for-phone-only {
	
			}
			@include for-tablet-portrait-up {
				
			}
			@include for-tablet-landscape-up {
						
			}
			@include for-desktop-up {
				column-gap: 15px;
			}
			@include for-big-desktop-up {

			}
		}

		&__controller-counter-current {
			font-size: 2.2rem;
			width: 25px;
			text-align: center;
			@include for-phone-only {
				font-size: 2.2rem;
			}
			@include for-tablet-portrait-up {
				font-size: 2.2rem;
			}
			@include for-tablet-landscape-up {
						
			}
			@include for-desktop-up {
				font-size: 2.8rem;
				padding-bottom: 15px;
			}
			@include for-big-desktop-up {

			}
		}

		&__controller-counter-outof {
			font-size: 1.2rem;
			color: #5B5B5D;
			padding-top: 5px;
		}
}

