@import "./media.scss";

.calculators {

		&__calculator {
			position: relative;
			&-wrapper {
				display: flex;
				@include for-phone-only {
					justify-content: center;
				}
				@include for-tablet-portrait-up {
				}
				@include for-tablet-landscape-up {
					width: 100%;	
				}
				@include for-desktop-up {
				}
				@include for-big-desktop-up {
				}
			}

			&-wrapper_odd {
				width: 100%;
				display: flex;
				justify-content: flex-end;
				@include for-phone-only {
					justify-content: center;
				}
				@include for-tablet-portrait-up {
					justify-content: start;
					
				}
				@include for-tablet-landscape-up {
					justify-content: flex-end;
				}
				@include for-desktop-up {
					justify-content: flex-end;
				}
				@include for-big-desktop-up {
					justify-content: flex-end;
				}
			}

			&-text-wrapper {
				box-sizing: content-box;
				width: 80%;
				display: flex;
				justify-content: start;
				flex-direction: column;
				@include for-phone-only {
					width: 100%;
				}
				@include for-tablet-portrait-up {
					width: 80%;
					
				}
				@include for-tablet-landscape-up {
					width: 50%;
				}
				@include for-desktop-up {
					width: 50%;
				}
				@include for-big-desktop-up {
					width: 50%;
				}
			}

			@include for-phone-only {
				height: 844px;
			}
			@include for-tablet-portrait-up {
				height: 1180px;
			}
			@include for-tablet-landscape-up {
				height: 820px;
			}
			@include for-desktop-up {
				height: 900px;
			}
			@include for-big-desktop-up {
				height: 900px;
			}	
		}

		&__text {
			display: flex;
			flex-direction: column;
			@include for-phone-only {
				margin-top: 90px;
			}
			@include for-tablet-portrait-up {
				margin-top: 168px;
			}
			@include for-tablet-landscape-up {
				margin-top: 168px;
				width: 90%;
			}
			@include for-desktop-up {
				margin-top: 100px;
			}
			@include for-big-desktop-up {

			}
		}

		&__text-title {
			
			@include for-phone-only {
				font-size: 2.2rem;
				align-self: center;
			}
			@include for-tablet-portrait-up {
				font-size: 2.66rem;
			}
			@include for-tablet-landscape-up {
				font-size: 2.4rem;
				display: inline-block;
			}
			@include for-desktop-up {
				font-size: 2.66rem;
				box-sizing: content-box;
			}
			@include for-big-desktop-up {

			}
		}

		&__text-describtion {
			

			@include for-phone-only {
				margin-top: 65px;
				font-size: 1rem;
				align-self: start;
			}
			@include for-tablet-portrait-up {
				margin-top: 78px;
				font-size: 1.2rem;
			}
			@include for-tablet-landscape-up {
				margin-top: 78px;
				width: 80%;
				font-size: 1rem;
			}
			@include for-desktop-up {
				font-size: 1rem;
				margin-top: 85px;
			}
			@include for-big-desktop-up {

			}
		}

		&__btn {
			display: block;
			user-select: none;
			text-align: center;
			background-color: #14221B;
			border: 1px solid #dedede;
			cursor: pointer;
			transition: all 0.2s;
			text-decoration: none;
			color: #dedede;

			&:hover {
				background-color: #12321e;
			}

			@include for-phone-only {
				margin: 380px auto 0 auto;
				width: 80%;
				padding: 20px 0;
			}
			@include for-tablet-portrait-up {
				margin-top: 135px;
				width: 80%;
				padding: 25px 0;
			}
			@include for-tablet-landscape-up {
				margin-top: 135px;
				width: 80%;
				padding: 20px 0;	
			}
			@include for-desktop-up {
				margin-top: 200px;
				width: 500px;
				padding: 30px 0;
			}
			@include for-big-desktop-up {

			}
		}

		&__imgs-img {

			&_pavingSlabs {
				position: absolute;
			}

			&_fences {
				position: absolute;
			}

			&_facadeTiles {
				position: absolute;
			}

			&_roof {
				position: absolute;
			}
		}
		&__line {
			width: 90%;
			margin: 0 auto;
			height: 1px;
			background-color: #fff;
		}
}

