@import "./media.scss";

.header {
	z-index: 100;
	padding: 20px 0;
	width: 100%;
	position: fixed;
	top: 0;
	left: 0;
	display: flex;
	justify-content: space-between;
	align-items: center;

	@include for-phone-only {
		background-color: #080909;
	}
	@include for-tablet-portrait-up {
		background-color: #080909;
	}
	@include for-tablet-landscape-up {
		background-color: #00000081;
		backdrop-filter: blur(20px);
	}


	&__logo {
		display: flex;
		align-items: center;
		justify-content: space-between;
		column-gap: 15px;
		margin-left: 5%;
		cursor: pointer;
	}

	&__logo-icon {
		width: 44px;
		height: 43px;
		@include for-phone-only {
			width: 44px;
			height: 43px;
		}
		@include for-tablet-portrait-up {
			width: 44px;
			height: 43px;
		}
		@include for-tablet-landscape-up {
				width: 44px;
			height: 43px;	
		}
		@include for-desktop-up {
			width: 44px;
			height: 43px;
		}
		@include for-big-desktop-up {

		}
	}

	&__logo-text {
		font-family: "Helvetica";
		font-weight: bold;
	}

	&__nav {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		position: fixed;
		height: 100%;
		width: 100%;
		top: 0;
		left: 100%;
		right: 0;
		bottom: 0;
		z-index: 50;
		overflow-y: auto;
		background-color: rgba(0, 0, 0, 0.952);
		transition: all 0.2s;

		@include for-phone-only {
			display: flex;
			flex-direction: column;
			justify-content: center;
			align-items: center;
			position: fixed;
			height: 100%;
			width: 100%;
			top: 0;
			left: 100%;
			right: 0;
			bottom: 0;
			z-index: 50;
			overflow-y: auto;
			background-color: rgba(0, 0, 0, 0.952);
			transition: all 0.2s;
		}
		@include for-tablet-portrait-up {
			font-size: 1.5rem;
			display: flex;
			flex-direction: column;
			justify-content: center;
			align-items: center;
			position: fixed;
			height: 100%;
			width: 100%;
			top: 0;
			left: 100%;
			right: 0;
			bottom: 0;
			z-index: 50;
			overflow-y: auto;
			background-color: rgba(0, 0, 0, 0.952);
			transition: all .2s;
		}
		@include for-tablet-landscape-up {
			all: unset;
			margin-right: 5%;
			height: 100%;
			display: flex;
			align-items: center;
			font-size: .8rem;
		}
		@include for-desktop-up {
			font-size: .9rem;
		}
		@include for-big-desktop-up {
			font-size: 1rem;
		}
	}

	&__nav-ul {
		@include for-phone-only {
			list-style: none;
			display: flex;
			flex-direction: column;
			align-items: center;
			row-gap: 50px;
			justify-content: space-between;
			padding: 0;
			text-transform: uppercase;
		}
		@include for-tablet-portrait-up {
			list-style: none;
			display: flex;
			flex-direction: column;
			align-items: center;
			row-gap: 50px;
			justify-content: space-between;
			padding: 0;
			text-transform: uppercase;
		}
		@include for-tablet-landscape-up {
			all: unset;
			list-style: none;
			display: flex;
			align-items: center;
			column-gap: 20px;
			justify-content: space-between;
			text-transform: uppercase;
		}
		@include for-desktop-up {
			column-gap: 30px;
		}
		@include for-big-desktop-up {
			column-gap: 50px;
		}
	}

	&__nav-text {
		text-decoration: none;
		color: #dedede;
		cursor: pointer;
		transition: all .2s;
		&:hover {
			color: #fff;
			transform: scale(1.02);
		}
	}

	&__nav-line {

		background-color: #C82434;
		@include for-phone-only {
			width: 30px;
			height: 1px;
		}
		@include for-tablet-portrait-up {
			width: 30px;
			height: 1px;
		}
		@include for-tablet-landscape-up {
			width: 1px;
			height: 15px;
		}
		@include for-desktop-up {
			
		}
		@include for-big-desktop-up {

		}
	}

	&__burger {
		margin-right: 5%;
		position: relative;
		z-index: 50;
		align-items: center;
		height: 20px;
		width: 30px;
		margin-right: 5%;

		&::before, &::after {
		content: "";
		position: absolute;
		height: 2px;
		width: 100%;
		background-color: #dedede;
		transition: all .3s ease 0s;
		}

		&::before {
			top: 0;
		}

		&::after {
			bottom: 0;
		}

		&.active {
			span {
				transform: scale(0);
			}
		}

		&.active::before {
			top: 50%;
			transform: rotate(-45deg) translate(0, -50%);
		}

		&.active::after {
			bottom: 50%;
			transform: rotate(45deg) translate(0, 50%);
		}	

		@include for-phone-only {
					display: flex;

		}
		@include for-tablet-portrait-up {
					display: flex;

		}
		@include for-tablet-landscape-up {
					display: none;

		}
		@include for-desktop-up {
			
		}
		@include for-big-desktop-up {

		}
	}

	&__burger-line {
		height: 2px;
		width: 100px;
		transform: scale(1);
		background-color: #dedede;
	}
}

.open {
	left: 0;
}